img {
    height: 200px;
    width: 200px;
    border-radius: 25px;
}

.card {
    display: inline-block;
    margin-right: 4%;
    margin-top: 4%;
    border: 2px solid #38ae24;
    border-radius: 25px;
    -webkit-box-shadow: 0px 0px 5px 3px #38ae24;
            box-shadow: 0px 0px 5px 3px #38ae24;
}

@media only screen and (max-width: 960px) {
    img {
        height: 200px;
        width: 250px;
    }
}


